import React, { useState } from 'react'
import PageContainer from '../../../../components/layout/PageContainer'
import dayjs from 'dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { CalendarPicker } from '@mui/x-date-pickers/CalendarPicker';

const AttendanceResist = () => {
  const [date, setDate] = useState(dayjs('2022-04-07'));

  return (
    <PageContainer width='large'>
      <div className="flex flex-row w-[1320px] justigy-between">
        <div className='flex justify-center relative basis-1/2'>
          <div className='absolute top-[125px]'>
            <LocalizationProvider dateAdapter={AdapterDayjs}>
              <CalendarPicker date={date} onChange={(newDate) => setDate(newDate)} />
            </LocalizationProvider>
          </div>
        </div>
        <div className='relative basis-1/2'>
          <div className='absolute top-[125px] w-[100%] h-[511px] bg-[#F5F5F5]'>
            
          </div> 
        </div>
      </div>
    </PageContainer>
  )
}

export default AttendanceResist